<template>
    <div class="controls">
        <v-card v-if="roomFull" class="player-btn red darken-4 white--text" dark>
            WAIT FOR A FREE SPOT 
        </v-card>

        <template v-else>
            <template v-if="roomState !== 'gameStarted' && roomState !== 'gameCountdown'">
                <v-card v-if="playerState === 'connected'" 
                        class="player-btn deep-purple darken-4 white--text" 
                        dark 
                        @click="playerReady">
                    READY
                </v-card>
                <v-card v-if="playerState === 'ready' && roomState === 'roomReady'" 
                        class="player-btn deep-purple darken-4 white--text"
                        dark 
                        @click="playerStartGame">
                    START GAME
                </v-card>
                <v-card v-if="playerState === 'ready' && roomState === 'roomOpened'" 
                        class="player-btn  deep-purple darken-4 white--text" 
                        dark>
                    WAITING FOR OTHER PLAYERS...
                </v-card>
            </template>
            <template v-if="roomState === 'gameStarted' || roomState === 'gameCountdown'">
                <v-card v-if="playerState !== 'playing' && playerState !== 'ready'" 
                        class="player-btn red darken-4 white--text" 
                        dark>
                    WAIT FOR ANOTHER ROUND 
                </v-card>
                <template v-if="playerState === 'playing' || playerState === 'ready'">
                    <SnakeControls v-if="selectedGame === 'snake'" :playerColor="playerColor" @moveSnake="sendDataToPublicScreen"/>
                    <PokerControls v-if="selectedGame === 'poker'" :playerColor="playerColor"/>
                </template>
            </template>
        </template>
    </div>

</template>

<script>
import Peer from 'peerjs'
import * as timesync from 'timesync'
import SnakeControls from './controls/SnakeControls.vue'
import PokerControls from './controls/PokerControls.vue'

export default {
  name: 'Controls',
  components: {
    SnakeControls,
    PokerControls
  },
  props: ['selectedGame','screenId'],

  data: () => ({
    //Game data
    playerState: 'connecting', //connected, ready, playing, gameOver
    roomState: '',
    roomFull: false,
    playerColor: '#FFFFFF',

    //PeerJS data
    peerId: '',
    publicScreenId: '',
    connection: null,

    //TimeSync data
    ts: null
  }),

  created() {
    if (this.screenId) {
        this.publicScreenId = this.screenId
        this.joinRoom()
    } else {
        this.publicScreenId = prompt("Please enter the screen's peer ID:")
        this.joinRoom()
    } 
  },

  methods: {
    sendDataToPublicScreen (data) {
        this.connection.send({...data, peerId: this.peerId});
    },

    playerReady() {
        //this.fullscreenMode()
        this.sendDataToPublicScreen({type: 'playerReady'})
    },

    playerStartGame () {
        this.sendDataToPublicScreen({type: 'playerStartGame'})
    },

    initializePeer() {
      this.peer = new Peer()
      this.peer.on('open', id => {
        this.peerId = id
      })
      this.peer.on('error', err => {
        this.$emit('mainScreen') 
        alert(''+err)
      })
    },

    joinRoom() {
        this.initializePeer()

        //Sets up timesync
        var ts = timesync.create({
            peers: [], 
            interval: 1000,
            delay: 200,
            timeout: 1000
        });

        ts.on('sync', state => {
            console.log('sync ' + state);
            if (state == 'start') {

                const openConnections = Object.keys(this.peer.connections).filter(id => {
                    return this.peer.connections[id].some(conn => {
                        return conn.open;
                    });
                });

                ts.options.peers = openConnections;
                console.log('syncing with peers [' + ts.options.peers.join(', ') + ']');
                if (ts.options.peers.length) {
                    console.log('syncing with ' + ts.options.peers.join(', ') + '...');
                }
            }
            if (state == 'end') {
                console.log('');
            }
        });

        ts.on('change', function (offset) {
            console.log('changed offset: ' + offset);
        });

        ts.send = (id, data, timeout) => {
            var all = this.peer.connections[id];
            var conn = all && all.filter(function (conn) { 
                return conn.open;
            })[0];

            if (conn) {
                console.log('TS', data)
                conn.send(data);
            } else {
                console.log(new Error('Cannot send message: not connected to ' + id).toString());
            }

            // Ignoring timeouts
            return Promise.resolve();console.log('send', id, data);

            var all = this.peer.connections[id];
            var conn = all && all.filter(function (conn) { 
                return conn.open;
            })[0];

            if (conn) {
                conn.send(data);
            } else {
                console.log(new Error('Cannot send message: not connected to ' + id).toString());
            }

            // Ignoring timeouts
            return Promise.resolve();
        };

        this.ts = ts

        this.peer.on('open', id => {
          this.connection = this.peer.connect(this.publicScreenId, {
            reliable: true
          })
          this.connection.on('open', () => {
            this.playerState = 'connected'
          })

          this.connection.on('error', () => {
             this.$emit('mainScreen')
             alert(''+err)
          })

          this.connection.on('data', data => {
            console.log('data received')
            ts.receive(this.connection.peer, data); 

            switch(data.type) {
                case 'roomState':
                    this.roomState = data.roomState
                    break
                case 'playerState':
                    this.playerState = data.playerState
                    if (this.playerState === 'ready') {
                        this.playerColor = data.playerColor
                    }
                    break
                case 'roomFull':
                    this.roomFull = true
            }
        })
        })
      },

    fullscreenMode () {
        const docElem = document.documentElement
        /* View in fullscreen */
        if (docElem.requestFullscreen) {
            docElem.requestFullscreen();
        } else if (docElem.mozRequestFullScreen) { /* Firefox */
            docElem.mozRequestFullScreen();
        } else if (elem.webkitRequestFullscreen) { /* Chrome, Safari and Opera */
            docElem.webkitRequestFullscreen();
        } else if (elem.msRequestFullscreen) { /* IE/Edge */
            docElem.msRequestFullscreen();
        }

        screen.orientation.lock("landscape-primary")

    }

  }
}
</script>

<style>

.controls {
    width: 100%;
    height: 100%;
}

.player-btn {
  width: 100%;
  height: 100% !important;
  font-size: 10vw !important;
  padding-top: 10%;
}

</style>

