#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

.main-container {
  height: 100vh;
}

.choose-game-btn {
  height:200px;
  padding:5%;
  font-size:5vw;
}

</style>

