<template>
    <v-row class="main-row">
        <v-col cols="12">
            <v-row class="row-100" justify="center">
                <v-col cols="4" justify="center">
                    <img src="img/card-back.svg" class="control-playing-card">
                </v-col>
                <v-col cols="4">
                    <img src="img/card-back.svg" class="control-playing-card">
                </v-col>
                <v-col cols="4">
                    <v-row class="control-poker-row">
                        <v-btn class="control-poker-btn" :color="playerColor">FOLD</v-btn>
                    </v-row>
                    <v-row class="control-poker-row">
                        <v-btn class="control-poker-btn" :color="playerColor">CALL</v-btn>
                    </v-row>
                    <v-row class="control-poker-row-raise">
                        <!-- <v-slider
                            min="1" max="1000"
                            v-model="raiseSlider"
                            class="control-poker-slider"
                            :color="playerColor"
                            thumb-label="always"
                        ></v-slider> -->
                        <v-btn class="control-poker-btn control-poker-btn-raise" :color="playerColor">RAISE</v-btn>
                    </v-row>
                </v-col>
            </v-row>
        </v-col>
    </v-row>
</template>

<script>

export default {
  name: 'PokerControls',
  props: ['playerColor'],
  data: () => ({
      raiseSlider: 2
  }),
  methods: {
    fold () {
        this.$emit('fold')
    },
    call () {
        this.$emit('call')
    },
    raise () {
        this.$emit('raise', {raiseAmount: this.raiseSlider})
    }
  }
}
</script>

<style>

.main-row {
  height: 100%
}

.control-playing-card {
    width: 100%;
}

.control-poker-btn {
    height: 90% !important;
    width: 100%;
    font-size: 3vw !important;
}

.control-poker-row {
    height: 33.3%;
    padding-right: 3%;
}

.control-poker-row-raise {
    height: 33.3%;
    padding-right: 3%;
}

.control-poker-btn-raise {
    height: 100% !important;
}

.control-poker-row-raise .v-input {
    height: 0%;
}

.control-poker-row-raise .v-input__control {
    margin-top: 15%;
}


</style>

