<script>
export default {
  name: 'Poker',
  props: ['players'],
  data: () => ({
    cards: [],
    dealer: 0,
    currentTurn: 1,
    currentPlayers: [],
    tableChips: 0
  }),
  created() {
      this.randomGame()
      this.currentPlayers = this.players;
      this.currentPlayers = this.currentPlayers.map(p => {
          return {...p, folded: false, action: 'WAITING', currentScore: p.score }
      })
      this.startGame()
  },

  computed: {
      activePlayers () {
          return this.currentPlayers.filter(p => !p.folded)
      }
  },

  methods: {
      randomGame() {
        let randomNums = []
        while (randomNums.length < (5 + this.players.length * 2)) {
            const randomNum = Math.floor(Math.random() * 52) + 1
            if (!randomNums.includes.randomNum) {
                randomNums.push(randomNum)
            }
        }
        this.cards = randomNums.map(rn => {
            let card = ''
            if (rn < 14) {
                card = 'H'
            } else if (rn < 27) {
                card = 'C'
            } else if (rn < 40) {
                card = 'D'
            } else if (rn < 53) {
                card = 'S'
            }
            card += (rn % 13) + 1
            return {
                card,
                shown: false
            }
        })
      },

      cardImage(index) {
          if (this.cards[index].shown) {
              return `img/${this.cards[index].card}.svg`
          } else {
              return `img/card-back.svg`
          }
      },

      showPlayerCards () {
          this.$emit('showPlayerCards', this.cards.map(c => c.card))
      },

      nextPlayer() {
          this.currentTurn = this.activePlayers.length === this.currentTurn ? 0 : this.currentTurn + 1
      },

      showPlayerAction(ind, action) {
          this.activePlayers[ind].action = action
      },

      startGame () {
          this.activePlayers[this.currentTurn].currentScore = this.activePlayers[this.currentTurn].currentScore - 1
          this.showPlayerAction(this.currentTurn, 'Small Blind 1')
          this.nextPlayer()
          this.activePlayers[this.currentTurn].currentScore = this.activePlayers[this.currentTurn].currentScore - 2
          this.showPlayerAction(this.currentTurn, 'Big Blind 2')
      }

  }
}
</script>

