<template>
    <div class="game-room">
        <v-row class="main-row">
            <v-col cols="12">
            <v-card class="main-card">
                <v-row class="game-room-row">
                    <v-col cols="8" class="justify-center align-center text-center game-screen">

                        <v-card class="game-card">

                            <div v-if="['roomOpened','roomReady','gameCountdown', 'gameEnded'].includes(roomState)" 
                                 class="waiting-card deep-purple darken-4 white--text">

                                <template v-if="roomState === 'roomOpened'">
                                    <h1>WAITING FOR PLAYERS...</h1>
                                    <br>
                                    <p>Scan the QR code or use the PEER ID to join the game:</p>
                                    <br>
                                    <img alt="QR code" :src="qrCodeUrl">
                                    <br>
                                    PEER ID: {{ peerId }}
                                    <br><br>
                                    <h3>CLICK 'READY' TO JOIN THE NEXT ROUND OF {{selectedGame.toUpperCase()}}</h3>
                                </template>
                                
                                <template v-if="roomState === 'roomReady'">
                                    <h3>CLICK 'READY' TO JOIN THE NEXT ROUND OF {{selectedGame.toUpperCase()}}</h3>
                                    <v-btn large class="mt-5 start-btn" 
                                           color="white deep-purple--text" 
                                           @click="startGameCountDown">
                                        START GAME
                                    </v-btn>
                                    <br>
                                    <br>
                                    <br>
                                    <p>Scan the QR code or use the PEER ID to join the game:</p>
                                    <br>
                                    <img alt="QR code" :src="qrCodeUrl">
                                    <br>
                                    PEER ID: {{ peerId }}
                                </template>

                                <template v-if="roomState === 'gameCountdown'">
                                    <h1>GAME STARTING IN</h1>
                                    <h1>{{countDown}}</h1>
                                </template>

                                <template v-if="roomState === 'gameEnded'">
                                    <h1>RESULTS:</h1>
                                    <v-row class="results-row">
                                        <v-list>
                                            <v-list-item
                                            v-for="player in results"
                                            :key="player.peerId"
                                            >
                                            <v-list-item-icon>
                                                <v-icon :color="player.color">person</v-icon>
                                            </v-list-item-icon>

                                            <v-list-item-content>
                                                <v-list-item-title>Player {{player.peerId}} - {{player.score}}</v-list-item-title>
                                            </v-list-item-content>

                                            </v-list-item>
                                        </v-list>
                                    </v-row>
                                    <v-btn large class="mt-5 start-btn new-game-btn" 
                                           color="white deep-purple--text" 
                                           @click="newGame">
                                        PLAY AGAIN
                                    </v-btn>

                                </template>

                            </div>                                

                            <Snake v-if="roomState === 'gameStarted' && selectedGame === 'snake'" 
                                    :players="players.filter(p => p.state === 'playing')"
                                    :connections="connections"
                                    @updateScore="updateScore"
                                    @gameOver="gameOver"/>
                            <Poker v-if="roomState === 'gameStarted' && selectedGame === 'poker'"
                                    :players="players.filter(p => p.state === 'playing')"/>

                        </v-card>

                    </v-col>
                    <v-col cols="4" class="align-end">
                        <div class="stats-side">
                            <v-row>
                                <h3>Players ({{ sortedPlayingPlayers.length }})</h3>
                            </v-row>
                            <v-row>
                                <v-list>
                                    <v-list-item
                                    v-for="player in sortedPlayingPlayers"
                                    :key="player.peerId"
                                    >
                                    <v-list-item-icon>
                                        <v-icon :color="player.color">person</v-icon>
                                    </v-list-item-icon>

                                    <v-list-item-content>
                                        <v-list-item-title>Player {{player.peerId}} ({{player.state}}) - {{player.score}}</v-list-item-title>
                                    </v-list-item-content>

                                    </v-list-item>
                                </v-list>
                            </v-row>
                            <v-row>
                                <h3>Viewers ({{ players.filter(p => p.state !== 'playing' && p.state !== 'ready').length }})</h3>
                            </v-row>
                            <v-row>
                                <v-list>
                                    <v-list-item
                                    v-for="player in players.filter(p => p.state !== 'playing' && p.state !== 'ready')"
                                    :key="player.peerId"
                                    >
                                    <v-list-item-icon>
                                        <v-icon :color="player.color">person</v-icon>
                                    </v-list-item-icon>

                                    <v-list-item-content>
                                        <v-list-item-title>Player {{player.peerId}} ({{player.state}}) </v-list-item-title>
                                    </v-list-item-content>

                                    </v-list-item>
                                </v-list>
                            </v-row>
                        </div>
                        <div v-if="roomState === 'gameStarted' || roomState === 'gameCountdown' || roomState === 'gameEnded'" 
                             class="qr-code-side">
                            <v-row class="row-100-width">
                                <p class="margin-auto">Scan the QR code to join the game:</p>
                            </v-row>
                            <v-row class="row-100-width">
                                <img alt="QR code" :src="qrCodeUrl"  class="margin-auto">
                            </v-row>
                            <v-row class="row-100-width">
                                <p  class="margin-auto">PEER ID: {{ peerId }}</p>
                            </v-row>
                        </div>
                    </v-col>
                </v-row>
            </v-card>
            </v-col>
        </v-row>
    </div>

</template>

<script>
import Peer from 'peerjs'
import Snake from './games/Snake.vue'
import Poker from './games/Poker.vue'
import * as timesync from 'timesync'

export default {
  name: 'GameRoom',
  components: {
    Snake,
    Poker
  },
  props: ['selectedGame'],
  data: () => ({
    //PeerJS data
    peerId: '',
    connections: {},
    peer: null,

    //Games
    games: {
        snake: {
            defaultScore: 0,
            minPlayers: 1,
            maxPlayers: 4
        },
        poker: {
            defaultScore: 1000,
            minPlayers: 1,
            maxPlayers: 10
        }
    },
    playerColors: ["#4DB6AC","#E57373","#64B5F6","#FFEE58","#BA68C8","#FFB74D","#4DB6AC","#F06292","#80DEEA","#AED581"],

    //Current game data
    roomState: 'roomOpened', // roomOpened, roomReady, gameCountDown, gameStarted, gameEnded
    players: [],
    countDown: 0,

    //Timesync
    ts: null

  }),

  created() {
    this.openRoom()
  },

  computed: {
    //Returns the QR code for accessing the gameroom
    qrCodeUrl: function() {
        if (!this.privateScreen) {
            return "https://api.qrserver.com/v1/create-qr-code/?data=" + 
            'https://stubnovalucia.github.io/king-sombra-games/' + '?game=' + 
            this.selectedGame + '%26screenid=' + this.peerId + "&size=200x200"
        }
    },
    readyPlayers() {
        return this.players.filter(p => p.state === 'ready')
    },
    //Players sorted by their score
    sortedPlayingPlayers() {
        const playingPlayers = this.players.filter(p => p.state === 'playing' || p.state === 'ready')
        playingPlayers.sort((p1,p2) => p2.score - p1.score)
        return playingPlayers
    }
  },

  watch: {
    readyPlayers() {
      if (this.readyPlayers.length >= this.games[this.selectedGame].minPlayers) {
          this.roomState = 'roomReady'
      }
    },
    roomState () {
        for (const [peerId, conn] of Object.entries(this.connections)) {
            const player = this.players.filter(p => p.peerId === peerId)
            if (player.length) {
                let data = {type: 'roomState', roomState: this.roomState }
                this.sendDataToPrivateScreens(['connected','ready','playing','gameOver'], data)
                if (this.roomState === 'gameEnded') {
                    this.sendDataToPrivateScreens(['connected','ready','playing','gameOver'], {type:'playerState', playerState: 'connected'})
                }
            } 
        }
    },
  },
  methods: {
    //Sends data to all private screens that are currently in one of the specified playerStates
    sendDataToPrivateScreens(playerStates, data) {
        for (const [peerId, conn] of Object.entries(this.connections)) {
            const player = this.players.filter(p => p.peerId === peerId && playerStates.includes(p.state))
            if (player.length) {
                conn.send(data)
            }
        }
    },

    //Initializes peer id for the screen
    initializePeer() {
      this.peer = new Peer()
      this.peer.on('open', id => {
        this.peerId = id
      })
      this.peer.on('error', err => {
        this.$emit('mainScreen')
        alert(''+err)
      })
    },

    openRoom() {
        this.initializePeer()

        //Sets up timesync
        var ts = timesync.create({
            peers: [], 
            interval: 1000,
            delay: 200,
            timeout: 1000
        });

        ts.on('sync', state => {
            console.log('sync ' + state);
            if (state == 'start') {

                const openConnections = Object.keys(this.peer.connections).filter(id => {
                    return this.peer.connections[id].some(conn => {
                        return conn.open;
                    });
                });

                ts.options.peers = openConnections;
                console.log('syncing with peers [' + ts.options.peers.join(', ') + ']');
                if (ts.options.peers.length) {
                    console.log('syncing with ' + ts.options.peers.join(', ') + '...');
                }
            }
        });

        ts.on('change', function (offset) {
            console.log('changed offset: ' + offset);
        });

        ts.send = (id, data, timeout) => {
            var all = this.peer.connections[id]; 
            var conn = all && all.filter(function (conn) { 
                return conn.open;
            })[0];

            if (conn) {
                conn.send(data);
            } else {
                console.log(new Error('Cannot send message: not connected to ' + id).toString());
            }

            // Ignoring timeouts
            return Promise.resolve();
        };

        this.ts = ts

        // show the system time and synced time once a second on screen
        setInterval(function () {
            console.log('System time: ' + new Date().toISOString().replace(/[A-Z]/g, ' '));
            console.log('Synced time: ' + new Date(ts.now()).toISOString().replace(/[A-Z]/g, ' '));
        }, 1000);

        this.peer.on('connection', c => {
            this.players.push({
                peerId: c.peer,
                state: 'connected',
                color: '#546E7A',
                score: this.games[this.selectedGame].defaultScore,
                position: -1
            })
            this.connections[c.peer] = c

            this.connections[c.peer].on('open', data => {
            if (this.readyPlayers.length === this.games[this.selectedGame].maxPlayers) {
                this.connections[c.peer].send({type:'roomFull'})
            }
            c.send({type: 'roomState', roomState: this.roomState})

            })

            this.connections[c.peer].on('data', data => {
                console.log('data received')
                ts.receive(this.connections[c.peer].peer, data); 
                switch(data.type) {
                    case 'playerReady':
                        if (this.readyPlayers.length < this.games[this.selectedGame].maxPlayers) {
                            const color = this.playerColors[this.getPlayerPosition()]
                            this.players = this.players.map(p => p.peerId === data.peerId ? {...p, state: 'ready', position: this.getPlayerPosition(), color} : p)
                            this.connections[c.peer].send({type:'playerState', playerState: 'ready', playerColor: color})
                            if (this.readyPlayers.length === this.games[this.selectedGame].maxPlayers) {
                                this.sendDataToPrivateScreens(['connected'],{type:'roomFull'})
                            }
                        } else {
                            this.connections[c.peer].send({type:'roomFull'})    
                        }
                        
                        break
                    case 'playerStartGame':
                        this.startGameCountDown()
                        break
                }
            })

            this.connections[c.peer].on('close', () => {
                this.players = this.players.filter(p => p.peerId !== c.peer)
                this.game = getGame('one');
                console.log('playing already and connecting')
            })
        })  

    },

    selectGame(game) {
        this.selectedGame = game
        this.gameSelected = true
    },

    getPlayerPosition () {
        let found = false
        let pos = 0
        while (!found) {
            if (!this.readyPlayers.filter(p => p.position === pos).length) {
                return pos
            }
            pos++
        }    
    },

    startGameCountDown() {
        this.roomState = 'gameCountdown'
        this.countDown = 10
        let counDownInterval = setInterval(() => {
        this.countDown -= 1
            if (this.countDown === 0) {
                clearInterval(counDownInterval);
                this.startGame()
            }
        },1000)
    },

    startGame() {
        this.players = this.players.map(p => p.state === 'ready' ? {...p, state: 'playing'} : p)
        this.sendDataToPrivateScreens(['playing'],{type:'playerState', playerState: 'playing'})
        this.roomState = 'gameStarted';
    },

    updateScore(position) {
        this.players = this.players.map(p => p.position === position ? {...p, score: p.score + 10} : p)
    },

    gameOver() {
        this.results = [...this.sortedPlayingPlayers]
        this.players = this.players.map(p => {
            return {...p, color: '#546E7A', state: 'connected', score: this.games[this.selectedGame].defaultScore}
        })
        this.roomState = 'gameEnded'
    },

    newGame () {
        this.roomState = 'roomOpened'
    },

  }
}
</script>

<style>
.main-card, .main-row {
  height: 100%
}

.game-room {
    width: 100%;
    height: 100%;
}

.game-screen {
    height: 100%;
}

.game-card {
    margin-left: 5%;
    width: 90%;
    height: 95%;
}

.game-room-row {
    height: 100%;
} 

.waiting-card {
    width: 100%;
    height: 100%;
    padding-top: 10%;
}

.start-btn {
    width: 300px !important;
    height: 100px !important;
    font-size: 2vw !important;
    background-color: white !important;
}

.results-row {
    width: 100%;
    padding-top: 5%;
    margin: 0 !important;
}

.results-row .v-list {
    margin: auto;
}

.stats-side {
    height: 60%;
    overflow-y: auto;
    overflow-x: hidden;
    padding-left: 4%;
}

.qr-code-side {
    height: 40%;
}

.row-100-width {
    width: 100%;
}

.margin-auto {
    margin: auto;
}

.new-game-btn {
    margin-top: 7% !important;
}

.v-list-item__title {
    text-align: left;
}

</style>

